"use client";

import { useState, useEffect } from "react";
import { motion } from "framer-motion";

export default function Home() {
  const [connected, setConnected] = useState(false);
  const [walletAddress, setWalletAddress] = useState("");

  // âœ… Check if already connected when page loads
  useEffect(() => {
    if (typeof window !== "undefined" && (window as any).ethereum) {
      (window as any).ethereum
        .request({ method: "eth_accounts" })
        .then((accounts: string[]) => {
          if (accounts.length > 0) {
            setWalletAddress(accounts[0]);
            setConnected(true);
          }
        });

      // âœ… Handle account change in MetaMask
      (window as any).ethereum.on("accountsChanged", (accounts: string[]) => {
        if (accounts.length > 0) {
          setWalletAddress(accounts[0]);
          setConnected(true);
        } else {
          setWalletAddress("");
          setConnected(false);
        }
      });
    }
  }, []);

  const connectWallet = async () => {
  try {
    if (typeof window.ethereum === "undefined") {
      alert("MetaMask is not installed!");
      return;
    }

    const accounts = await window.ethereum.request({
      method: "eth_requestAccounts",
    });

    console.log("Connected account:", accounts[0]);
  } catch (err: any) {
    console.error("MetaMask Connection Error:", err.message || err);
    alert("Failed to connect to MetaMask. Check console for details.");
  }
};



  return (
    <main className="flex min-h-screen flex-col items-center justify-center bg-gradient-to-br from-pink-500 via-purple-600 to-indigo-800 text-white px-6">
      {/* Title */}
      <motion.h1
        initial={{ opacity: 0, y: -40 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.8 }}
        className="text-5xl font-extrabold mb-6 text-center drop-shadow-lg"
      >
        ðŸ’– Matchmaking DApp
      </motion.h1>

      {/* Subtitle */}
      <motion.p
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ delay: 0.5, duration: 0.8 }}
        className="text-lg text-center max-w-2xl mb-10 text-pink-100"
      >
        Connect your wallet and start your journey to find your perfect match on
        blockchain ðŸ”—âœ¨
      </motion.p>

      {/* Wallet Connect Section */}
      {!connected ? (
        <motion.button
          whileHover={{ scale: 1.05 }}
          whileTap={{ scale: 0.95 }}
          onClick={connectWallet}
          className="px-8 py-4 bg-yellow-400 text-black font-semibold text-lg rounded-full shadow-xl hover:bg-yellow-300 transition"
        >
          ðŸ”— Connect Wallet
        </motion.button>
      ) : (
        <motion.div
          initial={{ scale: 0.8, opacity: 0 }}
          animate={{ scale: 1, opacity: 1 }}
          transition={{ duration: 0.6 }}
          className="bg-white/10 backdrop-blur-lg p-6 rounded-2xl shadow-2xl text-center"
        >
          <p className="mb-2 text-green-300 text-xl font-bold">
            âœ… Wallet Connected!
          </p>
          <p className="font-mono text-sm break-words text-yellow-200">
            {walletAddress}
          </p>
        </motion.div>
      )}
    </main>
  );
}
